/////////////////////////////////////////////////////////
// color default set
/////////////////////////////////////////////////////////

$basic-blue: #51bcd8;
$basic-white: #ffffff;
$support-blue-1: #b2dbe6;
$support-blue-2: #14a6cc;
$support-blue-3: #0e8cad;
$support-blue-4: #0b677e;
$background-color: #e9ebee;
$warning-babypink: #fff5f5;
$warning-blush-pink: #fe8b90;
$warning-watermelon: #f44c52;
$gray-50: #fafafa;
$gray-100: #f5f5f5;
$gray-200: #eeeeee;
$gray-300: #e0e0e0;
$gray-400: #bdbdbd;
$gray-500: #9e9e9e;
$gray-600: #757575;
$gray-700: #616161;
$gray-800: #424242;
$gray-900: #212121;


/////////////////////////////////////////////////////////
// 분기점
/////////////////////////////////////////////////////////
$md-container-width: 1024px;



/////////////////////////////////////////////////////////
//  txt default
/////////////////////////////////////////////////////////

%noto-thin {
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 100; }

%noto-light {
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 300; }

%noto-regular {
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 400; }

%noto-medium {
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 500; }

%noto-bold {
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 700; }

%noto-black {
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 800; }

%nanum-regular {
    font-family: 'NanumMyeongjo', serif;
    font-style: normal;
    font-weight: 400; }

%nanum-bold {
    font-family: 'NanumMyeongjo', serif;
    font-style: normal;
    font-weight: 600; }

%nanum-extrabold {
    font-family: 'NanumMyeongjo', serif;
    font-style: normal;
    font-weight: 700; }




/////////////////////////////////////////////////////////
//  common mixin
/////////////////////////////////////////////////////////

.txt-page-title {
    @extend %nanum-extrabold;
    font-size: 36px;
    text-align: center;
    color: $support-blue-2;
    @media screen and (min-width: $md-container-width) {
        font-size: 12px; } }

.txt-card-title {
    @extend %nanum-bold;
    font-size: 22px;
    text-align: center;
    color: $gray-700;
    @media screen and (min-width: $md-container-width) {
        font-size: 14px; } }

.txt-card-contents {
    @extend %nanum-bold;
    font-size: 14px;
    color: $gray-700;
    @media screen and (min-width: $md-container-width) {
        font-size: 12px; } }

.txt-card-date {
    @extend %nanum-bold;
    font-size: 12px;
    text-align: center;
    color: $gray-700;
    @media screen and (min-width: $md-container-width) {
        //나눔 레귤러로 바꾸기
        font-weight: 600;
        font-size: 10px; } }

/////////////////////////////////////////////////////////
//  button-txt
/////////////////////////////////////////////////////////

// 일반적인 버튼의 기본값은 txt-btn-default를 사용합니다.
// 특수한 경우에만 컬러를 변경합니다.

.txt-btn-default {
    @extend %noto-medium;
    font-size: 12px;
    text-align: center;
    color: $gray-700;
    @media screen and (min-width: $md-container-width) {
        font-family: 'NanumMyeongjo', serif;
        font-style: normal;
        font-weight: 700; } }

.txt-btn-default-disabled {
    @extend .txt-btn-default;
    color: $gray-400;
    @media screen and (min-width: $md-container-width) {
        font-family: 'NanumMyeongjo', serif;
        font-style: normal;
        font-weight: 700; } }

.txt-btn-solid {
    @extend .txt-btn-default;
    color: $basic-white;
    @media screen and (min-width: $md-container-width) {
        font-family: 'NanumMyeongjo', serif;
        font-style: normal;
        font-weight: 700; } }

.txt-btn-danger {
    @extend .txt-btn-default;
    color: $warning-watermelon;
    @media screen and (min-width: $md-container-width) {
        font-family: 'NanumMyeongjo', serif;
        font-style: normal;
        font-weight: 700; } }

.txt-btn-main {
    @extend %noto-medium;
    font-size: 18px;
    color: $gray-700;
    text-align: center;
    @media screen and (min-width: $md-container-width) {
        font-size: 12px; } }

.txt-btn-main-disabled {
    @extend .txt-btn-main;
    color: $gray-400;
    @media screen and (min-width: $md-container-width) {
        font-size: 12px; } }

.txt-btn-menu {
    @extend %noto-medium;
    font-size: 16px;
    color: $basic-white;
    text-align: center; }



/////////////////////////////////////////////////////////
//  desktop-main
/////////////////////////////////////////////////////////

.txt-main-date {
    @extend %nanum-extrabold;
    font-size: 24px;
    color: $gray-800;
    text-align: left;
    @media screen and (min-width: $md-container-width) {
        font-size: 12px;
        text-align: center; } }

.txt-main-title {
    @extend %nanum-regular;
    font-size: 90px;
    color: $gray-500;
    @media screen and (min-width: $md-container-width) {
        font-size: 38px;
        text-align: center; } }

.txt-main-caption {
    @extend %nanum-bold;
    font-size: 16px;
    color: $gray-800; }

