/////////////////////////////////////////////////////////
//  desktop-menu-left-popup
/////////////////////////////////////////////////////////

.txt-nav-username {
    @extend %noto-bold;
    font-size: 16px;
    text-align: center;
    color: $basic-white;
    @media screen and (min-width: $md-container-width) {
        font-size: 14px;
        text-align: left; } }

.txt-nav-usercaption {
    @extend %noto-medium;
    font-size: 14px;
    text-align: center;
    color: $basic-white; }

.txt-nav-footer {
    @extend %noto-bold;
    text-align: center;
    color: $basic-white;
    font-size: 12px;
    @media screen and (min-width: $md-container-width) {
        font-size: 10px; } }

/////////////////////////////////////////////////////////
//  desktop-slide (menu-right)
/////////////////////////////////////////////////////////

.txt-slide-search-date {
    @extend %noto-regular;
    font-size: 16px;
    color: $gray-800;
    @media screen and (min-width: $md-container-width) {
        font-size: 11px; } }

.txt-slide-date {
    @extend %noto-light;
    font-size: 52px;
    color: $basic-blue;
    text-align: right;
    @media screen and (min-width: $md-container-width) {
        font-size: 38px; } }

.txt-slide-time {
    @extend %noto-regular;
    font-size: 16px;
    color: $gray-700;
    text-align: right;
    @media screen and (min-width: $md-container-width) {
        font-size: 12px; } }

.txt-slide-title {
    @extend %noto-medium;
    font-size: 20px;
    color: $gray-700;
    text-align: right;
    @media screen and (min-width: $md-container-width) {
        font-size: 16px; } }

.txt-slide-pagination {
    @extend %noto-bold;
    font-size: 12px;
    color: $gray-600;
    text-align: center; }

